*,
*::before,
*::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app{
  width: 100vw;
  height: 100vh;
  background: url("../assets/background.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.window{
  position: fixed;
  width: 500px;
  height: 300px;
  background-color: rgba(255, 255, 255, 0.7);
  top: calc(50% - 150px);
  left: calc(50% - 250px);
  border-radius: 30px;
}